<template>
  <div class="list_search">
    <div class="search-box">
      <Form :model="searchForm" :label-width="80" class="search_form">
        <formItem v-for="item in searchArr" :key="item.label" :label="item.label">
          <Input v-model="searchForm[item.value]"
           v-if="item.type == 'input'"
           :placeholder="item.placeholder"></Input>

           <Select v-if="item.type == 'select'" v-model="searchForm[item.value]">
                <Option v-for="vo in item.arr"
                 :key="vo.value"
                 :value="vo.value">{{vo.label}}</Option>
            </Select>

            <CheckboxGroup v-if="item.type == 'checkbox'" v-model="searchForm[item.value]">
                <Checkbox v-for="vo in item.arr"
                 :key="vo.value"
                 :label="vo.label"></Checkbox>
            </CheckboxGroup>

            <RadioGroup v-if="item.type == 'radio'" v-model="searchForm[item.value]">
                <Radio v-for="vo in item.arr"
                 :key="vo.value" 
                 :label="vo.label"></Radio>
            </RadioGroup>

            <DatePicker v-if="item.type == 'datePicker'" v-model="searchForm[item.value]" 
              format="yyyy-MM-dd" 
              type="daterange" 
              placement="bottom-end" 
              :placeholder="item.placeholder" 
              style="width: 200px"></DatePicker>

        </formItem>
        <!-- <FormItem label="Input">
            <Input v-model="searchForm.input" placeholder="Enter something..."></Input>
        </FormItem>
        <FormItem label="Input">
            <Input v-model="searchForm.input" placeholder="Enter something..."></Input>
        </FormItem>
        <FormItem label="Select">
            <Select v-model="searchForm.select">
                <Option value="beijing">New York</Option>
                <Option value="shanghai">London</Option>
                <Option value="shenzhen">Sydney</Option>
            </Select>
        </FormItem>
        <FormItem label="DatePicker">
            <Row>
                <Col span="11">
                    <DatePicker type="date" placeholder="Select date" v-model="searchForm.date"></DatePicker>
                </Col>
                <Col span="2" style="text-align: center">-</Col>
                <Col span="11">
                    <TimePicker type="time" placeholder="Select time" v-model="searchForm.time"></TimePicker>
                </Col>
            </Row>
        </FormItem>
        <FormItem label="Radio">
            <RadioGroup v-model="searchForm.radio">
                <Radio label="male">Male</Radio>
                <Radio label="female">Female</Radio>
            </RadioGroup>
        </FormItem>
        <FormItem label="Checkbox">
            <CheckboxGroup v-model="searchForm.checkbox">
                <Checkbox label="Eat"></Checkbox>
                <Checkbox label="Sleep"></Checkbox>
                <Checkbox label="Run"></Checkbox>
                <Checkbox label="Movie"></Checkbox>
            </CheckboxGroup>
        </FormItem>
        <FormItem label="Switch">
            <i-switch v-model="searchForm.switch" size="large">
                <span slot="open">On</span>
                <span slot="close">Off</span>
            </i-switch>
        </FormItem> -->
        
        <!-- <FormItem label="Text">
            <Input v-model="searchForm.textarea" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="Enter something..."></Input>
        </FormItem> -->
      </Form>
    </div>
    <div class="buttons">
      <Button type="primary" @click="confirm">确定</Button>
      <Button style="margin-left: 8px">重制</Button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'list_search',
  props: {
    value: {
      /**
       * 格式
       searchFormData: {
          personalName: null, 
          companyName: null,
          status: null
        },
       */
      type: Object,
      required: true
    },
    searchArr: {
      // 格式
      /**
       * searchArr: [
          {type: 'input', value: 'personalName', label: '个人姓名', placeholder: '请输入个人姓名'},
          {type: 'input', value: 'companyName', label: '企业名称', placeholder: '请输入企业名称'},
          {type: 'select', value: 'status', label: '状态', arr: [
            {label: '通过', value: 1},
            {label: '未通过', value: 2}
          ], placeholder: '请选择'},
          {
          type: 'datePicker', value: 'datePeriod', label: '提交时间', placeholder: '选择时间'
          }
        ]
       */
      type: Array,
      required: true
    }
  },
  data () {
    return {
      searchForm: this.value,
    }
  },
  watch: {
    value (newVal) {
      console.log('watch:value', newVal)
      this.searchForm = newVal
    },
    searchForm (newVal) {
      console.log('watch:search', newVal)
      this.$emit('input', newVal)
    }
  },
  mounted () {
  },
  methods: {
    // 确定按钮
    confirm () {
      console.log(this.value)
      // 可以在emit 后添加参数也可以不用 因为因为组件已经使用了v-model
      this.$emit('searchConfirm')
    },
    // 重制按钮
    reset () {
      
    }
  }
}
</script>
<style lang="less" scoped>
.list_search {
  padding: 30px 20px 6px 20px;
  background-color: #fff;
  // display: flex;
  box-shadow:1px -2px 5px 0px rgba(242,242,242,0.5);
  border-radius:6px;
  .search_form {
    display: flex;
    flex-wrap: wrap;
    .ivu-form-item {
      margin-left: 10px;
      min-width: 300px;
      .ivu-form-item-label {
        color: #9CB2CD;
      }
    }
  }
  .buttons {
    margin-bottom: 10px;
    text-align: center;
  }
}
</style>